html,
body {
  height: 100%;
}
.navbar {
  font-weight: bold;
}
.navbar-toggle {
  padding: 10px 10px;
}
.navbar-brand {
  padding: 15px 15px;
}
.navbar-brand > img {
  height: 100%;
}
.navbar-lead {
  color: #fff;
  font-weight: normal;
  padding: 0 15px;
  background-color: #0f51a3;
}
.navbar-lead > h1,
.navbar-lead .h1,
.navbar-lead h2,
.navbar-lead .h2,
.navbar-lead h3,
.navbar-lead .h3 {
  margin: 13.33333333px 0;
}
.dropdown-menu {
  box-shadow: none;
}
.warning {
  background-color: #f0ad4e;
}
.danger {
  background-color: #d9534f;
}
.date {
  font-size: 0.8em;
  font-style: italic;
}
#wrap {
  min-height: 100%;
  height: auto !important;
  height: 100%;
}
